<template>
  <div class="container">
    <h1>Numbers!</h1>
    <div class="number__block">
      <div class="number__form">
        <div class="number__container">
          <span>Enter a number :</span>
          <form @submit.prevent="searchNumber">
            <input
              v-model="number"
              class="number__input"
              type="text"
              placeholder=""
            />
          </form>
        </div>
        <div class="number__random" @click="makeRand">rand</div>
      </div>

      <div class="number__description">
        <!--  <transition name="anim"> -->

        <transition-group
          tag="div"
          name="anim"
          v-for="(descr, index) in description"
          :key="index"
          class="number__layout"
        >
          <div :key="index" class="description__name">{{ index }}</div>

          <div
            :duration="500"
            :key="index"
            v-if="isLoad"
            class="description__text"
          >
            {{ descr }}
          </div>
        </transition-group>

        <!--  </transition> -->
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      number: "",
      description: {
        trivia: "Interesting number facts!",
        math: "Look at number with a new vision!",
        year: "What happened in this year?",
      },
      isLoad: Boolean,
    };
  },
  methods: {
    makeRand() {
      this.number = Math.floor(Math.random() * 2020) + 1;
      this.searchNumber();
    },
    async searchNumber() {
      try {
        this.isLoad = false;
        const responceTrivia = await axios.get(
          "http://numbersapi.com/" + this.number + "/trivia?notfound=ceil"
        );
        const responceMath = await axios.get(
          "http://numbersapi.com/" + this.number + "/math?notfound=ceil"
        );
        const responceYear = await axios.get(
          "http://numbersapi.com/" + this.number + "/year?notfound=ceil"
        );
        this.description.trivia = responceTrivia.data;
        this.description.math = responceMath.data;

        this.description.year = responceYear.data;
      } catch (e) {
        alert("Error!!!");
      } finally {
        this.isLoad = true;
      }
    },
  },
};
</script>

